import React from 'react'

import { Link, animateScroll as scroll } from "react-scroll"

export default () =>
    <div className='ui'>
        <div className='uiHeader'>
            <div className='uiHeaderLogo'>
                <div className='uiHeaderLogoTitle'>HenCan</div>
                <div className='uiHeaderLogoDescription'>Web Developer</div>
            </div>
            <div className='uiHeaderMenu'>
                <div className='uiHeaderMenuButton'>
                    <Link
                        activeClass="active"
                        to="welcome"
                        spy={true}
                        smooth={true}
                        offset={-70}
                        duration={500}
                    >
                        Home
                    </Link>
                </div>
                <div className='uiHeaderMenuButton'>
                    <Link
                        activeClass="active"
                        to="about"
                        spy={true}
                        smooth={true}
                        offset={-70}
                        duration={500}
                    >
                        Sobre
                    </Link>
                </div>
                <div className='uiHeaderMenuButton'>
                    <Link
                        activeClass="active"
                        to="portifolio"
                        spy={true}
                        smooth={true}
                        offset={-70}
                        duration={500}
                    >
                        Projetos
                    </Link>
                </div>
                <div className='uiHeaderMenuButton'>
                    <Link
                        activeClass="active"
                        to="contact"
                        spy={true}
                        smooth={true}
                        offset={-70}
                        duration={500}
                    >
                        Contato
                    </Link>
                </div>
            </div>
            <div className='uiBurger'>
                <div className='uiBurgerIcon' onClick={openCloseBurger}>
                    <div className='uiBurgerIconBar'></div>
                    <div className='uiBurgerIconBar'></div>
                    <div className='uiBurgerIconBar'></div>
                </div>
                <div className='uiBurgerMenu'>
                    <div className='uiBurgerMenuButton' onClick={openCloseBurger}>
                        <Link
                            activeClass="active"
                            to="welcome"
                            spy={true}
                            smooth={true}
                            offset={-70}
                            duration={500}
                        >
                            Home
                    </Link>
                    </div>
                    <div className='uiBurgerMenuButton' onClick={openCloseBurger}>
                        <Link
                            activeClass="active"
                            to="about"
                            spy={true}
                            smooth={true}
                            offset={-70}
                            duration={500}
                        >
                            Sobre
                    </Link>
                    </div>
                    <div className='uiBurgerMenuButton' onClick={openCloseBurger}>
                        <Link
                            activeClass="active"
                            to="portifolio"
                            spy={true}
                            smooth={true}
                            offset={-70}
                            duration={500}
                        >
                            Projetos
                    </Link>
                    </div>
                    <div className='uiBurgerMenuButton' onClick={openCloseBurger}>
                        <Link
                            activeClass="active"
                            to="contact"
                            spy={true}
                            smooth={true}
                            offset={-70}
                            duration={500}
                        >
                            Contato
                    </Link>
                    </div>
                </div>
                <div className='uiBurgerBackground' onClick={openCloseBurger}></div>
            </div>
        </div>
    </div>

var openMenu = 0
function openCloseBurger() {
    if (openMenu == 1) {
        document.querySelector(".uiBurgerMenu").style.right = -130 + "px"
        document.querySelector(".uiBurgerBackground").style.right = -200 + "vw"
        openMenu = 0
    } else {
        document.querySelector(".uiBurgerMenu").style.right = 0
        document.querySelector(".uiBurgerBackground").style.right = 0
        openMenu = 1
    }
}